<template>
<div id="qq">
    {{getfdata()}}
    <div id="qul">
   
                            <span class="heading">Population, Literacy rate  in {{citys}}</span>
                            <b-container fluid class="text-light text-center" v-for="dat in ssddata" v-bind:key="dat">
                                <span v-if="dat.city==city">
                       <b-row class="mb-4" align-h="centre">
                           <b-col md="4" class="p-6 " align="center">
                             <b-card
                                    title="Population"
                                    img-src="https://assets.sentinelassam.com/h-upload/2020/07/13/143870-popu.webp?w=400&dpr=2.6"
                                    img-alt="Image"
                                    img-top
                                    tag="article"
                                    style="max-width: 50rem;"
                                    class="mb-3"
                                >
                                    <b-card-text>
                                        {{dat.census[0].description}} in {{citys}} is {{dat.census[0].level.value}} <br>
                                        <b-progress :value="dat.census[0].level.value" :max="max" show-value class="mb-3"></b-progress><br>
                                        If you are looking for the city to live, crime is one of the most important accept.Here crime data is 
                                        analysis of previous data, and this percentage is average of the crime rate of that city.
                                        
                                        
                                    
                                    </b-card-text>

                                </b-card>
                           </b-col>
                       </b-row>
                            
                        

                       <b-row class="mb-4">
                           <b-col md="4" class="ml-md-auto p-6">
                            <b-card
                                    title="Sex ratio"
                                    img-src="https://familyinequality.files.wordpress.com/2010/03/male-and-male-in-dress-toilet-people-pink-blue1.jpg?w=584"
                                    img-alt="Image"
                                    img-top
                                    tag="article"
                                    style="max-width: 50rem;"
                                    class="mb-3"
                                >
                                    <b-card-text>
                                         {{dat.census[1].description}} in {{citys}} is {{dat.census[1].level.value}} (Females per 1000 males)<br>
                                          <b-progress :value="dat.census[1].level.value" :max="max" show-value class="mb-3"></b-progress><br>
                                            
                                        
                                    </b-card-text>

                                   
                            </b-card>
                           </b-col>
                       </b-row>

                        

                        <b-row class="mb-4">
                            <b-col md="4" class="p-6">
                            <b-card
                                    title="Literacy rate"
                                    img-src="https://swaddle-wkwcb6s.stackpathdns.com/wp-content/uploads/2019/08/Literacy-gender-gap-widening-Web-760x506.jpg"
                                    img-alt="Image"
                                    img-top
                                    tag="article"
                                    style="max-width: 50rem;"
                                    class="mb-3"
                                >
                                    <b-card-text>
                                         {{dat.census[2].description}} in {{citys}} is {{dat.census[2].level.value}}<br>
                                          <b-progress :value="dat.census[2].level.value" :max="max" show-value class="mb-3"></b-progress><br>
                                        If you are looking for the city to live, Cost of living is one of the most important accept.Here  data is 
                                        analysis of previous data, and this percentage is average of the data of that city.
                                    </b-card-text>

                                   
                                </b-card>

                            </b-col>
                        </b-row>

                                </span>
                            </b-container>


    </div>

</div>
    
</template>

<script>

export default {
    props:["citys"],
    
    
        data(){
            return{
                ssddata:"",
                city:this.citys,

            }

        },


    methods:{
        getfdata()
        {
           fetch("census.json")
        .then(response => response.json())
        .then(data => (this.ssddata = data));    
        }

    }
    
}
</script>
<style scoped>
#qul{
     margin-top: 30px;
  margin-bottom: 20px;
  margin-right: 30px;
  margin-left: 30px;
 
}
.heading{
    font-size: 40px;
    font-family: 'Courier New', Courier, monospace;
    text-align: center;
    font-weight: bold;
}
#qq{
    background: linear-gradient(265deg, #040404, #d8553b, #d8be3b, #3bcfd8, #5ad2da);
    background-size: 1000% 1000%;
    -webkit-animation: AnimationName 2s ease infinite;
    -moz-animation: AnimationName 2s ease infinite;
    -o-animation: AnimationName 2s ease infinite;
    animation: AnimationName 2s ease infinite;
}
@-webkit-keyframes AnimationName {
    0%{background-position:36% 0%}
    50%{background-position:65% 100%}
    100%{background-position:36% 0%}
}
@-moz-keyframes AnimationName {
    0%{background-position:36% 0%}
    50%{background-position:65% 100%}
    100%{background-position:36% 0%}
}
@-o-keyframes AnimationName {
    0%{background-position:36% 0%}
    50%{background-position:65% 100%}
    100%{background-position:36% 0%}
}
@keyframes AnimationName {
    0%{background-position:36% 0%}
    50%{background-position:65% 100%}
    100%{background-position:36% 0%}
}


</style>